/* code of value */

.orbit__codeofvalue{
    
    background: #070707;
   }
   .orbit__codeofvalue *{
       color: #fff;
   }
   .orbit__codeofvalue-header{
       width: 100%;
       height: 100%;
   }
   .orbit__codeofvalue-header-text {
    position: absolute;
    color: white; 
   left:100px;
  bottom: 15%;
 

  }
  .orbit__codeofvalue-header-text h1{
    font-size: 35px;
    padding-bottom: 10px;
    font-weight: 500;
  }
  .orbit__codeofvalue-header-text p{

opacity: 0.5;
margin-top: 10px;
font-size: 20px;
text-transform: capitalize;
line-height: 30px;
font-weight: 400;
  }


 
   .orbit__codeofvalue-header img{
   width: 100%;
   height: 100%;
   
   }
   
   .orbit__codeofvalue-img{
   width: 100%;
   text-align: center;
   }
   .orbit__codeofvalue-img img{
   
       width: 95%;
   }
   .orbit__codeofvalue-Text {
    margin: 100px  20% 0px 20%;
    padding-bottom: 150px;
   }
   .orbit__codeofvalue-Text p{
       font-size: 20px;
   
       line-height: 40px;
   font-weight: 300;

   }
   .link{
       margin-top:10px ;
   }
   .link a{
       text-decoration: underline;
   }
   .link a:hover{
      font-weight: 600;
   }