.orbit__latestnews{
    margin: 0 120px;
    padding-bottom: 150px;

}
.orbit__latestnews>span{
font-size: 20px;
}
.orbit__latestnews>p{
    margin-top: 40px;
    color: #070707;
font-size: 40px;
line-height: 70px;
}
.latest-textfield{
    font-size: 43px;
    margin-top: 15px;
    opacity: 0.17;
    border: none;
    border-bottom:3px solid grey ;
}
.latest-textfield:focus{
    border: none;
    border-bottom: 3px solid black;
}
.orbit__latestnews-row{
    width: 100%;
    margin-top: 90px;
display: flex;
justify-content: space-between;
}

.news-card span{
    margin-top: 30px;
font-size: 20px;
opacity: 0.39;
}
.news-img img{
    width: 100%;
}
.news-card{
    width: 30%;
}
.news-card p{
    font-weight: 500;
    margin-top: 30px;
    font-size: 22px;
  line-height: 40px;
  color: #070707;
}
.news-card a{
    margin-top: 50px;
    color: #070707;
}
.arrow{
    margin-top: 80px;
}

