.morebtn{
    border: none;
    background: none;
    color: #fff;
   }
   .morebtn:hover{
       cursor: pointer;
   }
   .orbit__navbar{
       display: flex;
       justify-content: space-between;
       align-items: center;
       margin-top: 60px;
       z-index: 1;
       position: absolute;
       width: 100%;
       }
       .orbit__navbar-logo{
       margin-left: 100px;
       
       }
       .orbit__navbar-logo img{
           width: 199px;
       height: 19px;
       }
       .orbit__navbar-links {
       
       display: flex;
       justify-content: flex-start;
       align-items: baseline;
       margin-right: 110px;
       
       }
       
       .orbit__navbar-links p{
           padding-left: 30px;
           
       }
       p{
           color: #fff;
       }
       .orbit__navbar-links p:hover{
           cursor: pointer;
          
       }
       
       a{
           font-size: 15px;
           color: #fff;
       }
   .orbit__modal-content{
       position: absolute;
       z-index: 1;
       overflow: hidden;
       background: #000;
       width: 100%;
       height: 100%;
       padding-left:100px ;
   }
   .modal-header{
       display: flex;
       justify-content: space-between;
       margin-right: 120px;
       margin-top: 60px;
   }
   .modal-header button{
       background: none;
       border: none;
       color: #fff;
   }
   .modal-header button{
       cursor: pointer;
   }
   .modal-links{
       margin-top: 100px;
   }
   .modal-links ul li{
       margin-top: 10px;

   }
   .modal-links ul li button{
       border: unset;
       background: unset;


   }
   .modal-links ul li button:hover{
text-decoration: underline;
   }
   .modal-links ul li h5{
       font-size: 20px;
   }
   .modal-list2{
       margin-top: 100px;
   }
   .modal-list2 li{
       margin-top: 10px;
   }
   .modal-list2 li h5{
       font-size: 15px;
       
   }