.badhairs{
    width: 100%;
    background: #070707;
   padding-bottom: 200px;
   color: #fff;
   
}

.btc {
    width: 100%;
    background: #fff;
    padding-bottom: 200px;
    padding-top: 100px;
}

.btc-title,.badhairs-title{
    padding-left:100px;
    padding-bottom: 80px;
}
.badhairs-title h1,.btc-title h1{
    
font-size: 40px;
}
.badhairs-title p,.btc-title p{
   margin-top: 10px;
    font-size: 25px;
    opacity: .8;
    line-height: 40px;
}
.badhairs-img ,.btc-img{
    width: 100%;
    text-align: center;

}
.badhairs-img img, .btc-img img{
    width: 85%;
}
.badhairs-content ,.btc-content{
    margin-top: 80px;
    justify-content: center;
}
.btc-content p{
    color: #070707;
}
.badhairs-content p ,.btc-content p{
  padding:10px 20% ;
  opacity: .8;
  line-height: 30px;
  font-weight: 300;
}
.quote ,.btc-quote{
    margin: 40px 10%;
}
.btc-quote span{
    color: #070707;
    font-size: 30px;
    font-weight: 400;
    line-height: 50px;
}
.quote span{
    color: #fff;
    font-size: 30px;
   
    font-weight: 400;
    line-height: 50px;
}
