.orbit__contact{
    width: 100%;
    background: #070707;
    font-weight: 300;
}
.orbit__contact *{
    color: #fff;
 }
 .orbit__contact-header-text
{  position: absolute;
    color: white; 
  
  left:100px;
  bottom: 15%;
 

} 
 .orbit__contact-header-text h1{
    font-size: 35px;
    padding-bottom: 10px;
    font-weight: 500;
}
.orbit__contact-header-text span{
    font-size: 30px;
    font-weight: 300;
    opacity: 0.58;
}
.orbit__contact-header-text p{
    width: 80%;
    font-weight: 300;

margin-top: 30px;
font-size: 15px;
text-transform: capitalize;
line-height: 30px;
font-weight: 300;
} 
.orbit__contact-header{
    width: 100%;
    height: 100%;

}
.orbit__contact-header img{
    width: 100%;
    height: 100%;
}
.orbit__contact-img{
    width: 100%;
    text-align: center;
}
.orbit__contact-img img{
    width: 95%;

}.orbit_contact-form{
    margin: 100px 20% 0 20%;
}

.orbit_contact-form-links h1{
    font-size: 30px;
}
.row{
  margin-top: 50px;
display: flex;
justify-content: space-between;
}
.orbit__contact-form-links > .row>button{
    background-color: #070707;
border: 1px solid #fff;
width: 350px;
height: 70px;
font-size: 15px;
font-weight: 300;

}
.orbit__contact-form-links > .row>button:hover{
border: 1px solid #0047ab ;
cursor: pointer;
}
.textfield{
    margin-top: 10px;
    width: 500px;
    background: none;
    box-shadow: none;
    border: none;
    border-bottom: 1px solid white;
    font-size: 40px;
    opacity: .5;

}
.textfield:focus{
    background: none;
    box-shadow: none;
    border: none;
}
.inq h2{
    margin-top: 80px;
    font-size: 20px;

}
.inq p{
    margin-top: 20px;
    font-size: 22px;
}
.margin{
    margin-top: 80px;
}
.inq h1{
font-size: 20px;
font-weight: 300;
}
.inq p{
    font-size: 20px;
    font-weight: 300;
    line-height: 30px;

}
.inq{
    padding-bottom: 50px;
}