
.orbit__services{
    padding: 180px 0px;
    background: var(--color-bg);
   
}

.orbit__services-what-we-do{
    margin-left: 100px;
}
.orbit__services-what-we-do p
{
    margin-top: 50px;
   font-size: 30px;
   line-height: 40px;
}

.orbit__services-what-we-do span
{
    
    padding-top: 100px;
    color: #FFFFFF;
    font-size: 20px;
    opacity: 1;
}
.orbit__services-links
{
    margin-top: 90px;
    text-align: center;
}
.orbit__services-links-row{

    margin-top: 90px;
    display: flex;
    
    justify-content: space-between;
    margin-left: 100px;
    margin-right: 120px;
}
.orbit__services-links-card :not(img) {
    width: 250px;
    text-align: left;
}
.orbit__services-links-card img{
    width: 80px;
    height: 100px;
}
.orbit__services-links-card h1{
    margin-top: 30px;
text-align: left;
font-size: 20px;
letter-spacing: 0px;
color: #FFFFFF;
opacity: 1;
}
.orbit__services-links-card span{

    font-size: 18px;
    letter-spacing: 0px;
    color: #FFFFFF;
    opacity: 0.61;
    text-align: left;
}
.orbit__services-links-card p{
    margin-top: 30px;
    font-size: 18px;
    letter-spacing: 0px;
    color: #FFFFFF;
    opacity: 0.61;
    line-height: 35px;
    
}
.orbit__services-links-card >.text{
    margin-top: 50px;
}
.margin200{
    margin-bottom: 200px;
}